<template>
	 <v-container>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-card-title primary-title>
            <div>
              <div class="headline">Upload anything, from anywhere.</div>
              <span class="grey--text"></span>
            </div>
          </v-card-title>

          <v-slide-y-transition>
            <v-card-text>
            	<div v-if="!isInitial">
              	<div v-if="isUploading">
              		<span v-if="uploadPercentage == 100">
              			<span> {{fileName}}, <small> {{fileSize}} kb</small></span>
              			<span> Upload Completed </span> 
              			<v-btn @click="undoUpload()"><i class="material-icons">undo</i></v-btn>
              		</span>
              		<span v-else>Uploading...{{uploadPercentage}} %  
              			<v-progress-circular
								      :value="uploadPercentage"
								      color="primary"
								    >
								    </v-progress-circular>
              		</span>
              		
              		<!-- <progress max="100" :value.prop="uploadPercentage"></progress> -->
              	</div>
              	<div v-else>
              		<div v-if="gettingFileSize">
	              		<span> {{fileName}}<br><small>Waiting for file size</small></span>
	              		<v-progress-circular
								      indeterminate
								      color="primary"
								    ></v-progress-circular>
								   </div> 
              		<div v-else>
              			<v-btn @click="reset()"><i class="material-icons">cancel_presentation</i></v-btn>
              			<span> {{fileName}}, <small> {{fileSize}} kb</small></span>
              			<v-btn @click="submitFile()"><i class="material-icons">cloud_upload</i></v-btn>
              		</div>
              	</div>
                
                <br>
              </div>
              <div class="dropbox">
                <input type="file" id="file" ref="file" @change="handleFileUpload()" class="input-file">

                  <div v-if="isInitial">
                    Drag your file(s) here to begin<br> or click to browse
                  </div>
              		<div v-else>
              			<img class="preview" :ref="'image'"/>
              		</div>
              </div>
          </v-card-text>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import * as axios from 'axios';
  export default {
  	data() {
  		return {
  			file: '',
  			fileName: '',
  			fileSize: 0,
  			uploadPercentage: 0,
  			isInitial: true,
  			isUploading: false,
  			UploadingComplete: false,
  			gettingFileSize: false
  		}
  	},
		methods: {
			handleFileUpload () {
				this.file = this.$refs.file.files[0]
				this.fileName = this.file.name
				this.fileSize = this.file.size
				this.fileSize = parseInt(this.fileSize/1024)
				//console.log(this.fileName,this.fileSize)
				this.getImagePreviews()
				this.isInitial = false
			},
			getImagePreviews () {
        
        let reader = new FileReader()
        reader.addEventListener("load", function (){
          this.$refs['image'].src = reader.result
        }.bind(this), false);
        reader.readAsDataURL( this.file )
    	},
			submitFile () {
				this.isUploading = true
				let formData = new FormData()
				formData.append('file', this.file)
				let url = 'https://www.alegralabs.com/vue/api/fileupload/fileupload.php'
				axios.post( url,
			    formData,
			    {
			      headers: {
			          'Content-Type': 'multipart/form-data'
			      },
			      onUploadProgress: function( progressEvent ) {
			        this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
			      }.bind(this)
			    }
			  ).then(function(data){
			  	//this.UploadingComplete = true
			    //console.log(data)
			  })
			  .catch(function(error){
			    console.log(error)
			  })
			}, 
			reset () {
				this.file = '',
				this.isInitial = true,
				this.$refs['image'].src = null
			},
			undoUpload () {
				this.isUploading = false
			}
		}
  }
</script>
<style>
  .dropbox {
    outline: 2px dashed grey; /* the dash box */
    outline-offset: -10px;
    background: lightcyan;
    color: dimgray;
    padding: 10px 10px;
    min-height: 200px; /* minimum height */
    position: relative;
    cursor: pointer;
  }

  .input-file {
    opacity: 0; /* invisible but it's there! */
    width: 100%;
    height: 200px;
    position: absolute;
    cursor: pointer;
  }

  .dropbox:hover {
    background: lightblue; /* when mouse over to the drop zone, change color */
  }

  .dropbox div {
    font-size: 1.2em;
    text-align: center;
    padding: 50px 0;
  }
</style>